header {
	padding: 28px;
	max-width: 100vw;
	justify-self: start;
	margin-right: auto;
  @media screen and (min-width: 900px) {
		// align-items:start;
    width: calc((830 * 100%)/1440);
    padding-top: calc((35 * 100%)/800);
    padding-bottom: calc((40 * 100%)/800);
		padding-left: calc((160 * 100%)/1440);
		padding-right: calc((210 * 100%)/1440);
		margin-right: 0;
	}

	img {
		height: 22px;

    @media screen and (min-width: 900px) {
		height: 35px;
	}
	}

	
}
