html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  min-height: 100vh;
  max-width: 100vw;
  font-family: "Josefin Sans", sans-serif;
  overflow-x: hidden;
  padding-bottom: 30px;
}
@media screen and (min-width: 900px) {
  body {
    align-items: start;
    max-height: 100vh;
    flex-wrap: wrap;
    overflow-y: hidden;
    background-image: url(../images/bg-pattern-desktop.svg);
    background-repeat: no-repeat;
    background-size: 57.6388888889% 100%;
  }
}

.attribution {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 11px;
  text-align: center;
}
@media screen and (min-width: 900px) {
  .attribution {
    left: 25%;
    transform: translateX(-25%);
  }
}

.attribution a {
  color: #3e52a3;
}

header {
  padding: 28px;
  max-width: 100vw;
  justify-self: start;
  margin-right: auto;
}
@media screen and (min-width: 900px) {
  header {
    width: 57.6388888889%;
    padding-top: 4.375%;
    padding-bottom: 5%;
    padding-left: 11.1111111111%;
    padding-right: 14.5833333333%;
    margin-right: 0;
  }
}
header img {
  height: 22px;
}
@media screen and (min-width: 900px) {
  header img {
    height: 35px;
  }
}

.hero-img {
  width: 100vw;
  background-color: #555;
}
@media screen and (min-width: 900px) {
  .hero-img {
    order: 1;
    width: 42.3611111111%;
    min-height: 100vh;
  }
  .hero-img picture {
    height: 0px;
  }
}
.hero-img img {
  width: 100vw;
  max-height: 250px;
  object-fit: cover;
  object-position: top;
}
@media screen and (min-width: 900px) {
  .hero-img img {
    width: 100%;
    min-height: 100vh;
    object-position: revert;
  }
}

.info {
  padding-top: 58px;
  max-width: 99vw;
  overflow: hidden;
  margin-bottom: 35px;
}
@media screen and (min-width: 900px) {
  .info {
    width: 57.6388888889%;
    padding-left: 11.1111111111%;
    padding-right: 14.5833333333%;
    margin-right: 0;
    margin-bottom: 3.125%;
  }
}
.info__title {
  text-align: center;
  text-transform: uppercase;
  text-align: center;
  font-size: 2.5em;
  font-weight: 300;
  letter-spacing: 10px;
  line-height: 1.05;
  word-break: break-word;
  max-width: 80vw;
  margin-left: auto;
  margin-right: auto;
  color: #ce9797;
  margin-bottom: 20px;
}
@media screen and (min-width: 900px) {
  .info__title {
    text-align: left;
    letter-spacing: 20px;
    max-width: 100%;
  }
}
@media screen and (min-width: 1400px) {
  .info__title {
    font-size: 4em;
  }
}
.info__title span {
  font-weight: 600;
  color: #413a3a;
  display: block;
}
.info__text {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  line-height: 1.5;
  font-size: 0.875rem;
  color: #ce9797;
}
@media screen and (min-width: 900px) {
  .info__text {
    text-align: left;
    max-width: 95%;
    font-size: 1rem;
    margin-left: 0;
    line-height: 1.7;
  }
}

.contact-form {
  width: 82vw;
  max-width: 375px;
  position: relative;
  height: 48px;
  margin-bottom: 30px;
}
@media screen and (min-width: 900px) {
  .contact-form {
    margin-left: 11.1111111111%;
    margin-right: 14.5833333333%;
    max-width: 31.25%;
    height: 56px;
  }
}
.contact-form input {
  width: 100%;
  height: 100%;
  border: 1px solid #ce9797;
  border-radius: 25px;
  padding-left: 25px;
  color: #be7474;
}
.contact-form input::placeholder {
  color: #be7474;
}
@media screen and (min-width: 900px) {
  .contact-form input {
    padding-left: 40px;
    font-size: 0.9375rem;
  }
}
.contact-form .form-submit {
  position: absolute;
  top: 0;
  right: 0;
  background: linear-gradient(to right, #f8bfbf, #ee8c8c);
  appearance: none;
  height: 100%;
  width: 65px;
  border: 0;
  border-radius: 25px;
  transition: all 0.2s ease;
}
@media screen and (min-width: 900px) {
  .contact-form .form-submit {
    width: 100px;
  }
  .contact-form .form-submit:hover {
    cursor: pointer;
    transform: scale(1.15);
    background: linear-gradient(to right, #f9c8c8, #ef9595);
    box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.5);
  }
}
.contact-form .form-error {
  position: absolute;
  top: 55px;
  left: 25px;
  color: #df0101;
}
@media screen and (min-width: 900px) {
  .contact-form .form-error {
    top: 65px;
    left: 40px;
  }
}
.contact-form .error-outline {
  outline: 1px solid #df0101;
}
.contact-form .success {
  color: #01df0c;
}
.contact-form .form-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 70px;
  color: #df0101;
}
@media screen and (min-width: 900px) {
  .contact-form .form-icon {
    right: 110px;
  }
}
.contact-form .form-icon.is-active {
  display: block;
  animation: show-message 0.7s 1 normal 0s ease-in-out both;
}
.contact-form .none {
  display: none;
}
.contact-form .form-error.is-active {
  display: block;
  animation: show-message 0.7s 1 normal 0s ease-in-out both;
}
@keyframes show-message {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}